    <!-- 支付 -->
    <pay ref="Pay"></pay>

    <!-- 审批 -->
    <hs-alert :show.sync="showAlert" :title="vettingContent === 1 ? $t('base.ApprovedPass') : $t('base.ApprovalRejection')" cancelBtn="" @confirm="_examineSubmit" maskClose>
      <div class="hs-alert-content" slot="content">
        <input maxlength='50' v-model="vettingMemo" :placeholder="$t('base.PleaseEnterAComment')"/>
