      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          font-weight bold
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms

  // 价格明细
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px

  // 航班信息
  .segmentDetails
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    height 80%
    overflow hidden
    border-radius 10px 10px 0 0

    >.header
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 45px
      border-bottom 1px solid $color_gray_layer
      padding 0 15px
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
      >p
        font-size 1.1rem
        font-weight bold

    >.details
      width 100%
      height calc(100% - 45px)
      overflow hidden
      .item
        width 100%
        padding 15px
        border-bottom 1px solid $color_gray_layer
        &:last-child
          border-bottom-width 0
        >.item-header
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          >.mark
            font-size 1rem
            color $color_theme
            font-weight bold
            padding 5px
            border-radius 2px
          >p
            font-size 1rem
            margin-left 5px
            line-height 1.2em

  // 审批
  .hs-alert-content
    width 100%
    padding 0 20px
    >input
      width 100%
      height 44px
      border 1px dashed $color_gray_layer0
      border-radius 2px
      font-size 0.9rem
      text-align center
</style>



