<template>
  <div class="segment">
    <div class="item" v-for="(item, index) in  datalist" :key="index">
      <div class="node">
        <div>
          <div class="dot"></div>
          <div class="time">
            <p>{{item.DepartureShortTime}}</p>
            <p class="small" v-if="index != 0">{{item.DepartureTime}}</p>
          </div>
          <p class="airport">{{item.DepartAirportName}}&nbsp;{{item.DepartTerminal}}</p>
        </div>
      </div>
      <div class="infor">
        <div>
          <p class="time">飞行{{item.FlightTime}}</p>
          <div class="plane">
            <div class="logo" :style="{'background-image': `url(${item.AirLineLogoUrl})`}"></div>
            <p>{{item.AirLineName}}{{item.FlightNo}}&nbsp;|&nbsp;{{item.Plane}}</p>
          </div>
        </div>
      </div>
      <template v-if="item.StopInfoList && item.StopInfoList.length > 0">
        <div class="stop" v-for="(itemStop, indexStop) in item.StopInfoList" :key="`stop${indexStop}`">
          <div>
            <div class="content">
              <p>经停</p>
              <p>{{itemStop.AirportName}}停留{{itemStop.Duration}}</p>
            </div>
          </div>
        </div>
      </template>
      <div class="node">
        <div>
          <div class="dot"></div>
          <div class="time">
            <p>{{item.ArrivalShortTime}}</p>
            <p class="small" v-if="index !== (datalist.length - 1)">{{item.ArrivalTime}}</p>
          </div>
          <p class="airport">{{item.ArrivalAirportName}}&nbsp;{{item.ArrivalTerminal}}</p>
        </div>
      </div>
      <div class="transfer" v-if="item.TransferCityName && index < (datalist.length - 1)">
        <div>
          <div class="content">
            <p>中转</p>
            <p>{{item.TransferCityName}}中转&nbsp;停留{{item.TransferTime}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    datalist: {
      type: Array,
      default: function() {
        return []
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.segment
  width 100%
  padding 20px 0
  >.item
    width 100%
    >.node
      width 100%
      >div
        @extend .flex-center
        width 10px
        height 10px
        margin-left 100px
        position relative
        >.dot
          width 10px
          height 10px
          border-radius 5px
          border 1px solid $color_theme
          background-color $color_white
          z-index 9
        >.time
        >.airport
          position absolute
        >.time
          width 85px
          left -95px
          >p
            width 100%
            text-align right
            line-height 1.2em
            font-weight bold
          >.small
            font-size 0.7rem
            color $color_gray_text
            position absolute
            top -1.2em
        >.airport
          font-weight bold
          width 200px
          left 20px
    >.infor
      width 100%
      height 85px
      >div
        @extend .flex-center
        width 0
        height 100%
        border-right 4px solid $color_theme
        margin-left 103px
        position relative
        >.time
          position absolute
          width 85px
          text-align right
          left -95px
          color $color_theme
          font-size 0.7rem
        >.plane
          @extend .flex-row
          @extend .flex-start
          @extend .no-wrap
          position absolute
          background-color $color_white
          border 1px solid $color_gray_layer
          border-radius 5px
          font-size 0.8rem
          color $color_gray_text
          padding 5px
          left 22px
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            margin-right 3px
    >.stop
    >.transfer
      width 100%
      height 100px
      >div
        @extend .flex-center
        width 0
        height 100%
        border-right 4px dashed $color_theme
        margin-left 103px
        position relative
        >.content
          @extend .flex-row
          @extend .flex-start
          height 30px
          position absolute
          left -15px
          >p
            font-size 0.7rem
            height 100%
            &:nth-child(1)
              @extend .flex-center
              text-align center
              width 16px
              border-radius 3px
              border 1px solid $color_theme
              color $color_theme
              background-color $color_white
              position relative
              left 10px
            &:nth-child(2)
              @extend .flex-row
              @extend .flex-start
              @extend .no-wrap
              background-color $color_gray_layer
              border-radius 3px
              padding 0 20px
    >.stop
      height 60px
      >div
        @extend .flex-center
        @extend .flex-top
        border-right 4px solid $color_theme
</style>



